import Vue from 'vue/dist/vue.esm.js'
import VueRouter from 'vue-router'

import login from '../pages/login.vue'
import unit from '../pages/unit/unitlist.vue'
import user from '../pages/user/userlist.vue'
import employeecard from '../pages/expiration/employeecard.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/UserManager/UserList' },
  { path: '/login', component: login }, // 
  { path: '/CompanyManager/CompanyList', component: unit }, // 
  { path: '/UserManager/UserList', component: user }, // 
  { path: '/ExpireManager/ExpireEmployeeCard', component: employeecard }, // 
  { path: '/ExpireManager/ExpireContract', component: employeecard }, // 
  { path: '/ExpireManager/ExpireRetire', component: employeecard }, // 
]

const router = new VueRouter({
  // mode: 'hash',
  mode: 'history',
  routes
})

export default router
